
if(window.innerWidth >= 992) showActiveLink();

function showActiveLink() {
    const getId = (link) => link.getAttribute("href").replace('#', '');

    const observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
            if(entry.isIntersecting){
                document.querySelectorAll('.--link').forEach(link => {
                    link.classList.toggle('--active', getId(link) === entry.target.id);
                });
            }
            
        }), {
            
            threshold: 0.75,
        }
    });

    document.querySelectorAll('section').forEach(section => {
        observer.observe(section)
    });

    document.querySelector('.nav__list').addEventListener('click', (event) => {
        if(event.target.classList.contains('--link')){
            event.preventDefault();
        }
        window.scrollTo({
            top: document.getElementById(getId(event.target)).offsetTop,        
            behavior: 'smooth',
        })
    });
}



