function changeIMG() {
  const image=['./img/products/3.jpg','./img/products/5.jpg','./img/products/7.jpg']
  

  btnadd('.goods-card__color-brown', image[0]);
  btnadd('.goods-card__color-purpure', image[1]);
  btnadd('.goods-card__color-orange', image[2]); 
  btnadd('.goods-card__color-red', image[0]);
  btnadd('.goods-card__color-dark', image[1]);
  btnadd('.goods-card__color-gray', image[2]); 
  function btnadd(btn, way) {
    if(document.querySelector(btn)){
      document.querySelector(btn).addEventListener("click", () => {
        document.querySelector('.goods-card__img>img').src= way;
      })
    }
  }
    

  slider();
  function slider() {
    const slides = document.querySelectorAll('.goods-card__slide');
    for(let index = 0; index < slides.length; index++) {
      let slide = slides[index];   
      slide.addEventListener("mouseover", (e) => {
        document.querySelector('.goods-card__img>img').src= e.currentTarget.src;
      })
    }   
  }
}

if(document.querySelector('.filter')) showFilter();

function showFilter() {
  const filter = document.querySelector('.filter');
  document.querySelector('.catalog__top-btn').addEventListener('click', () => {
    filter.classList.add('filter--active');
    document.body.classList.add('lock');
  })
  document.querySelector('.filter__top-close').addEventListener('click', () => {
    filter.classList.remove('filter--active')
    document.body.classList.remove('lock')
  })
  document.querySelector('.filter__bottom-btn-on').addEventListener('click', () => {
    filter.classList.remove('filter--active')
    document.body.classList.remove('lock')
  })
}



showBurger();
function showBurger() {
  const burger = document.querySelector('.nav__burger');
  const menu = document.querySelector('.nav__menu');

  burger.addEventListener('click', () =>  {      
    menu.classList.toggle('nav__menu--active');
    burger.classList.toggle('nav__burger--active');
    document.body.classList.toggle('lock');      
  });
  document.querySelectorAll('.nav__list-link').forEach(item =>{
    item.addEventListener('click', () =>{
 
    burger.classList.remove('nav__burger--active');
    menu.classList.remove('nav__menu--active');
    document.body.classList.remove('lock');  
    });
  })
};